/**
 * Ïù∏Íµ¨/ÏÑ∏ÎåÄ Íµ¨Ï°∞ Î∂ÑÏÑù Îç∞Ïù¥ÌÑ∞
 * Î∂ÄÎèôÏÇ∞ ÏàòÏöîÏùò ÌïµÏã¨ ÎèôÏù∏Ïù∏ Ïù∏Íµ¨ Î∞è ÏÑ∏ÎåÄ Íµ¨Ï°∞ Î≥ÄÌôî Îç∞Ïù¥ÌÑ∞
 */

// Ïó∞Î†πÎ≥Ñ Ïù∏Íµ¨ Î∂ÑÌè¨ Îç∞Ïù¥ÌÑ∞ (2025ÎÖÑ Í∏∞Ï§Ä)
export const generatePopulationPyramid = () => {
    const ageGroups = [
        '0-9', '10-19', '20-29', '30-39', '40-49', '50-59', '60-69', '70-79', '80+'
    ];

    return ageGroups.map((age, index) => {
        // 2025ÎÖÑ ÌïúÍµ≠ Ïù∏Íµ¨ Íµ¨Ï°∞ Î∞òÏòÅ (Ï†ÄÏ∂úÏÇ∞, Í≥†Î†πÌôî)
        let maleRatio, femaleRatio;

        switch (age) {
            case '0-9':
                maleRatio = 3.8; femaleRatio = 3.5;
                break;
            case '10-19':
                maleRatio = 4.5; femaleRatio = 4.2;
                break;
            case '20-29':
                maleRatio = 6.8; femaleRatio = 6.2;
                break;
            case '30-39':
                maleRatio = 6.5; femaleRatio = 6.0;
                break;
            case '40-49':
                maleRatio = 8.2; femaleRatio = 7.8;
                break;
            case '50-59':
                maleRatio = 8.8; femaleRatio = 8.5;
                break;
            case '60-69':
                maleRatio = 7.5; femaleRatio = 7.8;
                break;
            case '70-79':
                maleRatio = 4.2; femaleRatio = 5.0;
                break;
            case '80+':
                maleRatio = 1.8; femaleRatio = 3.2;
                break;
            default:
                maleRatio = 5; femaleRatio = 5;
        }

        return {
            ageGroup: age,
            male: -maleRatio, // ÏùåÏàòÎ°ú ÌëúÏãú (ÌîºÎùºÎØ∏Îìú ÏôºÏ™Ω)
            female: femaleRatio,
            malePercent: maleRatio,
            femalePercent: femaleRatio,
        };
    });
};

// ÏÑ∏ÎåÄ Íµ¨ÏÑ± Ï∂îÏù¥ Îç∞Ïù¥ÌÑ∞ (1Ïù∏Í∞ÄÍµ¨, Îã§Ïù∏Í∞ÄÍµ¨)
export const generateHouseholdData = () => {
    const data = [];

    for (let year = 2010; year <= 2026; year++) {
        // 1Ïù∏ Í∞ÄÍµ¨ ÎπÑÏú® Ï¶ùÍ∞Ä Ï∂îÏÑ∏
        let singleRatio = 23.9 + (year - 2010) * 1.2;
        if (year >= 2020) singleRatio += 2; // ÏΩîÎ°úÎÇò Ïù¥ÌõÑ Í∏âÏ¶ù
        if (year >= 2025) singleRatio = Math.min(singleRatio, 35); // 2025ÎÖÑ 35% Í∑ºÏ†ë

        // 2Ïù∏ Í∞ÄÍµ¨ ÎπÑÏú®
        const coupleRatio = 26 + (year - 2010) * 0.3;

        // 3Ïù∏ Í∞ÄÍµ¨ ÎπÑÏú®
        const threePersonRatio = 21 - (year - 2010) * 0.5;

        // 4Ïù∏ Ïù¥ÏÉÅ Í∞ÄÍµ¨ ÎπÑÏú®
        const fourPlusRatio = 100 - singleRatio - coupleRatio - threePersonRatio;

        data.push({
            year,
            single: parseFloat(singleRatio.toFixed(1)),
            couple: parseFloat(coupleRatio.toFixed(1)),
            threePerson: parseFloat(Math.max(threePersonRatio, 12).toFixed(1)),
            fourPlus: parseFloat(Math.max(fourPlusRatio, 15).toFixed(1)),
            totalHouseholds: Math.round(19000 + (year - 2010) * 300 + Math.random() * 100), // Ï≤ú ÏÑ∏ÎåÄ
        });
    }

    return data;
};

// ÏßÄÏó≠Î≥Ñ Ïù∏Íµ¨ ÏàúÏù¥Îèô Îç∞Ïù¥ÌÑ∞
export const generateMigrationData = () => {
    const regions = [
        { id: 'seoul', name: 'ÏÑúÏö∏', lat: 37.5665, lng: 126.9780 },
        { id: 'gyeonggi', name: 'Í≤ΩÍ∏∞', lat: 37.4138, lng: 127.5183 },
        { id: 'incheon', name: 'Ïù∏Ï≤ú', lat: 37.4563, lng: 126.7052 },
        { id: 'busan', name: 'Î∂ÄÏÇ∞', lat: 35.1796, lng: 129.0756 },
        { id: 'daegu', name: 'ÎåÄÍµ¨', lat: 35.8714, lng: 128.6014 },
        { id: 'daejeon', name: 'ÎåÄÏ†Ñ', lat: 36.3504, lng: 127.3845 },
        { id: 'gwangju', name: 'Í¥ëÏ£º', lat: 35.1595, lng: 126.8526 },
        { id: 'ulsan', name: 'Ïö∏ÏÇ∞', lat: 35.5384, lng: 129.3114 },
        { id: 'sejong', name: 'ÏÑ∏Ï¢Ö', lat: 36.4800, lng: 127.2890 },
        { id: 'gangwon', name: 'Í∞ïÏõê', lat: 37.8228, lng: 128.1555 },
        { id: 'chungbuk', name: 'Ï∂©Î∂Å', lat: 36.6357, lng: 127.4917 },
        { id: 'chungnam', name: 'Ï∂©ÎÇ®', lat: 36.6588, lng: 126.6728 },
        { id: 'jeonbuk', name: 'Ï†ÑÎ∂Å', lat: 35.8203, lng: 127.1089 },
        { id: 'jeonnam', name: 'Ï†ÑÎÇ®', lat: 34.8161, lng: 126.4629 },
        { id: 'gyeongbuk', name: 'Í≤ΩÎ∂Å', lat: 36.5760, lng: 128.5056 },
        { id: 'gyeongnam', name: 'Í≤ΩÎÇ®', lat: 35.4606, lng: 128.2132 },
        { id: 'jeju', name: 'Ï†úÏ£º', lat: 33.4996, lng: 126.5312 },
    ];

    return regions.map(region => {
        let netMigration;

        // 2025ÎÖÑ Í∏∞Ï§Ä Ïù∏Íµ¨ ÏàúÏù¥Îèô (Ï≤ú Î™Ö)
        switch (region.id) {
            case 'seoul':
                netMigration = -85; // ÏÑúÏö∏ ÏàúÏú†Ï∂ú
                break;
            case 'gyeonggi':
                netMigration = 120; // Í≤ΩÍ∏∞ ÏàúÏú†ÏûÖ
                break;
            case 'incheon':
                netMigration = 15;
                break;
            case 'sejong':
                netMigration = 25; // ÏÑ∏Ï¢Ö ÏßÄÏÜç Ïú†ÏûÖ
                break;
            case 'busan':
            case 'daegu':
                netMigration = -15 + Math.random() * 10;
                break;
            default:
                netMigration = -10 + Math.random() * 20 - 10;
        }

        return {
            ...region,
            netMigration: Math.round(netMigration),
            inflow: Math.round(Math.abs(netMigration) + 50 + Math.random() * 30),
            outflow: Math.round(Math.abs(netMigration) + 50 - netMigration + Math.random() * 30),
            migrationRate: parseFloat((netMigration / 100).toFixed(2)), // Ïù∏Íµ¨ ÎåÄÎπÑ %
        };
    });
};

// Ïó∞ÎèÑÎ≥Ñ ÏßÄÏó≠ Ïù∏Íµ¨ ÏàúÏù¥Îèô Ï∂îÏù¥
export const generateMigrationTrend = () => {
    const data = [];

    for (let year = 2015; year <= 2025; year++) {
        // ÏÑúÏö∏ Ïú†Ï∂ú, Í≤ΩÍ∏∞ Ïú†ÏûÖ Í∏∞Ï°∞
        let seoulNet = -50 - (year - 2015) * 5;
        let gyeonggiNet = 80 + (year - 2015) * 8;

        if (year >= 2020) {
            seoulNet -= 20; // ÏΩîÎ°úÎÇò Ïù¥ÌõÑ ÌÉàÏÑúÏö∏ Í∞ÄÏÜç
            gyeonggiNet += 15;
        }

        data.push({
            year,
            seoul: seoulNet,
            gyeonggi: gyeonggiNet,
            incheon: 10 + Math.round(Math.random() * 10),
            busan: -10 - Math.round(Math.random() * 5),
            other: -gyeonggiNet - seoulNet - 10,
        });
    }

    return data;
};

// Ï£ºÌÉù ÏàòÏöî ÏòàÏ∏° Îç∞Ïù¥ÌÑ∞
export const generateHousingDemandForecast = () => {
    const data = [];

    for (let year = 2024; year <= 2035; year++) {
        // ÏÑ∏ÎåÄ Ïàò Ï¶ùÍ∞Ä Í∏∞Î∞ò ÏàòÏöî Ï∂îÏ†ï
        const baseHouseholds = 22000; // 2024ÎÖÑ ÏÑ∏ÎåÄ Ïàò (Ï≤ú)
        const householdGrowth = year <= 2028 ? 1.5 : year <= 2032 ? 0.8 : 0.3; // Ï¶ùÍ∞ÄÏú® Ï≤¥Í∞ê

        const households = baseHouseholds * Math.pow(1 + householdGrowth / 100, year - 2024);
        const newDemand = households * (householdGrowth / 100) * 0.8; // Ïã†Í∑ú ÏàòÏöî (80%)
        const replacementDemand = households * 0.01; // Î©∏Ïã§ ÎåÄÏ≤¥ ÏàòÏöî (1%)

        data.push({
            year,
            households: Math.round(households),
            newDemand: Math.round(newDemand),
            replacementDemand: Math.round(replacementDemand),
            totalDemand: Math.round(newDemand + replacementDemand),
            supplyForecast: Math.round(350 + Math.random() * 50), // ÏòàÏÉÅ Í≥µÍ∏â (Ï≤ú Ìò∏)
        });
    }

    return data;
};

// ÏÉùÏÇ∞Í∞ÄÎä•Ïù∏Íµ¨ Ï∂îÏù¥ (15-64ÏÑ∏)
export const generateWorkingAgePopulation = () => {
    const data = [];

    for (let year = 2010; year <= 2040; year++) {
        let workingAgePop; // Ï≤ú Î™Ö

        if (year <= 2017) {
            workingAgePop = 37000 + (year - 2010) * 100; // Ï†ïÏ†ê
        } else {
            workingAgePop = 37700 - (year - 2017) * 200; // Í∞êÏÜå
        }

        const totalPop = year <= 2028 ? 51700 + (year - 2020) * 50 : 52000 - (year - 2028) * 100;

        data.push({
            year,
            workingAgePop: Math.round(workingAgePop),
            totalPop: Math.round(totalPop),
            ratio: parseFloat(((workingAgePop / totalPop) * 100).toFixed(1)),
            isProjection: year > 2025,
        });
    }

    return data;
};

// Ï£ºÏöî ÏßÄÌëú ÏöîÏïΩ
export const getPopulationSummary = () => {
    return {
        totalPopulation: 51700, // Ï≤ú Î™Ö (2025ÎÖÑ)
        totalHouseholds: 23500, // Ï≤ú ÏÑ∏ÎåÄ
        singleHouseholdRatio: 34.5, // %
        averageHouseholdSize: 2.2, // Î™Ö
        workingAgeRatio: 70.2, // %
        elderlyRatio: 19.1, // % (65ÏÑ∏ Ïù¥ÏÉÅ)
        seoulPopChange: -0.8, // % (Ï†ÑÎÖÑ ÎåÄÎπÑ)
        gyeonggiPopChange: 1.2, // %
        lastUpdated: '2025-12-01',
    };
};

export default {
    generatePopulationPyramid,
    generateHouseholdData,
    generateMigrationData,
    generateMigrationTrend,
    generateHousingDemandForecast,
    generateWorkingAgePopulation,
    getPopulationSummary,
};
